import React, { useEffect, useState, useCallback, useRef } from "react";
import { ChevronRight, List } from "lucide-react";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible";
import { Button } from "@/components/ui/button";

interface TableOfContentsProps {
  tableOfContents: string;
  className?: string;
}

const TableOfContents: React.FC<TableOfContentsProps> = ({
  tableOfContents,
  className = "",
}) => {
  const [activeId, setActiveId] = useState<string>("");
  const [isOpen, setIsOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const tocRef = useRef<HTMLDivElement>(null);

  //     
  const handleTocClick = useCallback(
    (e: React.MouseEvent<HTMLDivElement>) => {
      const target = e.target as HTMLElement;
      const link = target.closest('a[href^="#"]') as HTMLAnchorElement;

      if (link) {
        e.preventDefault();
        const href = link.getAttribute("href");
        if (href) {
          const id = decodeURIComponent(href.replace("#", ""));
          const element = document.getElementById(id);

          if (element) {
            const offsetTop = element.offsetTop - 100;
            window.scrollTo({
              top: offsetTop,
              behavior: "smooth",
            });
          }

          //    
          if (isMobile) {
            setIsOpen(false);
          }
        }
      }
    },
    [isMobile]
  );

  //   
  useEffect(() => {
    const checkIsMobile = () => {
      setIsMobile(window.innerWidth < 768);
      //   
      if (window.innerWidth >= 768) {
        setIsOpen(true);
      }
    };

    checkIsMobile();
    window.addEventListener("resize", checkIsMobile);
    return () => window.removeEventListener("resize", checkIsMobile);
  }, []);

  // IntersectionObserver    
  useEffect(() => {
    if (!tableOfContents || !tableOfContents.trim()) {
      return;
    }

    // IntersectionObserver  
    let observer: IntersectionObserver | null = null;

    // DOM       
    const timer = setTimeout(() => {
      //     
      const headings = Array.from(
        document.querySelectorAll("h1, h2, h3, h4, h5, h6")
      );

      if (!headings.length) {
        return;
      }

      //   IntersectionObserver 
      observer = new IntersectionObserver(
        entries => {
          //   
          const visibleHeadings = entries
            .filter(entry => entry.isIntersecting)
            .sort(
              (a, b) => a.boundingClientRect.top - b.boundingClientRect.top
            );

          if (visibleHeadings.length > 0) {
            const activeHeading = visibleHeadings[0]!.target as HTMLElement;
            setActiveId(activeHeading.id);
          }
        },
        {
          //   
          rootMargin: "-100px 0px -50% 0px",
          threshold: 0.1,
        }
      );

      //   
      headings.forEach(heading => {
        if (heading.id) {
          observer?.observe(heading);
        }
      });
    }, 100); // 100ms 

    return () => {
      clearTimeout(timer);
      if (observer) {
        observer.disconnect();
      }
    };
  }, [tableOfContents]);

  //    
  useEffect(() => {
    if (!tocRef.current) return;

    const links = tocRef.current.querySelectorAll('a[href^="#"]');

    links.forEach(link => {
      const href = link.getAttribute("href");
      if (href) {
        const id = decodeURIComponent(href.replace("#", ""));
        if (id === activeId) {
          link.classList.add("toc-active");
        } else {
          link.classList.remove("toc-active");
        }
      }
    });
  }, [activeId]);

  if (!tableOfContents || !tableOfContents.trim()) {
    return null;
  }

  const TocContent = () => (
    <ScrollArea className="h-full  pr-3">
      <div
        ref={tocRef}
        className="toc-content"
        dangerouslySetInnerHTML={{ __html: tableOfContents }}
        onClick={handleTocClick}
      />
    </ScrollArea>
  );

  return (
    <div className={`sticky top-24 ${className}`}>
      {isMobile ? (
        <Collapsible open={isOpen} onOpenChange={setIsOpen}>
          <CollapsibleTrigger asChild>
            <Button
              variant="outline"
              className="w-full justify-between mb-4"
              size="sm"
            >
              <div className="flex items-center space-x-2">
                <List className="w-4 h-4" />
                <span></span>
              </div>
              <ChevronRight
                className={`w-4 h-4 transition-transform duration-200 ${
                  isOpen ? "rotate-90" : ""
                }`}
              />
            </Button>
          </CollapsibleTrigger>
          <CollapsibleContent className="space-y-2">
            <div className="border rounded-lg p-4 bg-card">
              <TocContent />
            </div>
          </CollapsibleContent>
        </Collapsible>
      ) : (
        <div className="border rounded-lg p-4 bg-card/50 backdrop-blur-sm shadow-sm">
          <div className="flex items-center space-x-2 mb-4">
            <List className="w-4 h-4" />
            <span className="font-semibold text-sm"></span>
          </div>
          <Separator className="mb-4" />
          <TocContent />
        </div>
      )}
    </div>
  );
};

export default TableOfContents;
